}

a {
  color: #333;
  text-decoration: none;
}

li {
  list-style: none;
}

.wrap {
  width: 80vw;
  margin: 0 auto;
}

.on {
  display: block;
}

.off {
  display: none;
}

.modal-open {
  overflow: hidden;
}

#admin #create h2, #admin #modify h2 {
  font-size: 20px;
}

#admin #create form, #admin #modify form {
