@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

a {
  color: #333;
  text-decoration: none;
}

li {
  list-style: none;
}

.wrap {
  width: 80vw;
  margin: 0 auto;
}

.on {
  display: block;
}

.off {
  display: none;
}

.modal-open {
  overflow: hidden;
}

#admin table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

#admin table thead {
  background-color: #eee;
}

#admin table thead th {
  padding: 10px;
  text-align: center;
}

#admin table thead th:nth-child(2) {
  width: 150px;
}

#admin table tbody tr:nth-child(2n) {
  background-color: #eee;
}

#admin table tbody tr td {
  padding: 10px;
  text-align: center;
  text-overflow: ellipsis;
}

#admin table tbody tr td input {
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}

#admin table tbody tr td input[value='수정'] {
  background-color: #53b2ff;
}

#admin table tbody tr td input[value='삭제'] {
  background-color: #c42c2c;
}

#admin table tbody tr td input:hover {
  background-color: #ec6718;
}

#admin .modal {
  background-color: rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

#admin .modal #modify, #admin .modal #create {
  width: 60%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#admin .modal #modify h2, #admin .modal #create h2 {
  margin-bottom: 20px;
}

#admin .modal #modify input, #admin .modal #create input {
  padding: 5px;
}

#admin .modal #modify .flex, #admin .modal #create .flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 200px;
  margin: 20px auto;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

#admin .modal #modify .flex input, #admin .modal #create .flex input {
  width: 80px;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

#admin .modal #modify .flex input:hover, #admin .modal #create .flex input:hover {
  background-color: #ec6718;
  color: #fff;
}
