@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
.container {
  width: 80%;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 445px) {
  .container {
    min-width: 300px;
  }
}

.off {
  display: none;
}

.modal-open {
  overflow: hidden;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

#app #sub .topBanner {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #DEECF9;
}

#app #sub .topBanner h2 {
  font-size: 72px;
  font-weight: bold;
  margin: 50px 0 30px;
}

#app #sub .topBanner h3 {
  font-size: 32px;
}

#app #sub .topBanner h4 {
  font-weight: 400;
}

#app #sub .topBanner input[type='button'] {
  width: 250px;
  padding: 20px 0;
  border: none;
  background-color: #0f152e;
  color: #fff;
  border-radius: 5px;
  margin-top: 50px;
}

#app #sub .topBanner div {
  text-align: center;
  margin: 0 auto;
}

#app #sub .flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

#app #sub #contents {
  padding: 60px 0;
}

#app #sub #contents h4 {
  font-weight: bold;
  font-size: 30px;
}

#app #sub #contents #sInfo {
  margin-bottom: 80px;
}

#app #sub #contents #sInfo .flex {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0 auto;
}

#app #sub #contents #sInfo .flex div {
  width: 290px;
  height: 210px;
  padding: 20px 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}

#app #sub #contents #sInfo .flex div:nth-child(2) {
  width: 292px;
  position: relative;
}

#app #sub #contents #sInfo .flex div:nth-child(2):before, #app #sub #contents #sInfo .flex div:nth-child(2):after {
  content: '';
  display: block;
  width: 2px;
  height: 100%;
  background-color: #ccc;
  position: absolute;
  top: 0;
}

#app #sub #contents #sInfo .flex div:nth-child(2):before {
  left: 0;
}

#app #sub #contents #sInfo .flex div:nth-child(2):after {
  right: 0;
}

#app #sub #contents #sInfo .flex div img {
  height: 90px;
  margin-bottom: 15px;
}

#app #sub #contents #sInfo .flex div .title {
  font-size: 22px;
  font-weight: 400;
}

#app #sub #contents #sInfo .flex div .desc {
  font-size: 18px;
}

#app #sub #contents #dInfo h4 {
  border-bottom: 1px solid #333;
  padding-bottom: 20px;
}

#app #sub #contents #dInfo table tr th {
  margin-right: 30px;
  padding-right: 30px;
  vertical-align: top;
  font-size: 20px;
}

#app #sub #contents #dInfo table tr td {
  font-size: 16px;
  line-height: 35px;
  white-space: pre-wrap;
  padding-bottom: 30px;
}
