@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

a {
  color: #333;
  text-decoration: none;
}

li {
  list-style: none;
}

.wrap {
  width: 80vw;
  margin: 0 auto;
}

.on {
  display: block;
}

.off {
  display: none;
}

.modal-open {
  overflow: hidden;
}

#admin #create h2, #admin #modify h2 {
  font-size: 20px;
}

#admin #create form, #admin #modify form {
  width: 100%;
  margin: 0 auto;
}

#admin #create form .f_flex, #admin #modify form .f_flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 5px;
}

#admin #create form .f_flex label, #admin #modify form .f_flex label {
  display: inline-block;
  width: 150px;
  font-size: 14px;
  text-align: right;
  margin-right: 20px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#admin #create form .f_flex input, #admin #modify form .f_flex input {
  width: calc(100% - 250px);
  border: none;
  background-color: #eee;
  padding: 5px 10px;
}

#admin #create form .f_flex input:focus, #admin #modify form .f_flex input:focus {
  outline: none;
}

#admin #create form .f_flex textarea, #admin #modify form .f_flex textarea {
  width: calc(100% - 250px);
  height: 60px;
  border: none;
  background-color: #eee;
}
