@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

a {
  color: #333;
  text-decoration: none;
}

li {
  list-style: none;
}

.wrap {
  width: 80vw;
  margin: 0 auto;
}

.on {
  display: block;
}

.off {
  display: none;
}

.modal-open {
  overflow: hidden;
}

#admin header {
  height: 60px;
  background-color: #0f152e;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#admin header h1 {
  height: 40px;
}

#admin header h1 img {
  width: 150px;
  margin-left: 20px;
}

#admin main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: calc(100vh - 60px);
}

#admin main aside {
  display: block;
  width: 200px;
  height: 100%;
  background-color: #0f152e;
  text-align: center;
  border-top: 1px solid #fff;
}

#admin main aside a {
  display: block;
  color: #fff;
  border-bottom: 1px solid #fff;
  padding: 20px;
}

#admin main #contents {
  width: calc(100vw - 200px);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
}

#admin main #contents h2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#admin main #contents h2::before {
  content: ' ';
  display: inline-block;
  width: 3px;
  height: 25px;
  background-color: #ec6718;
  margin-right: 10px;
}
